.menu {
  margin-top: 60px !important;
  background-color: #171923;
  padding: 1vh 29px 0 24px;
  min-height: calc(100vh - 60px);
  display: flex;
  justify-content: center;
}

.menu-items {
  margin-top: 30px;
}

.selected-value-row {
  display: flex;
}

.selected-value {
  color: #6bc909;
  font-size: 18px;
}

.menu > .container > .button-box {
  margin: auto;
  margin-top: 10px;
  width: 250px;
}

.menu > .container > .button-box:hover {
  color: #09090e !important;
}

.menu > .container > .bottom-line {
  margin: 30px 0 24px;
}

.menu > .container > .menu-items > .notification {
  margin-bottom: 0px !important;
}

.menu > .container > .App > .page-link {
  font-weight: 600;
  font-size: 16px;
  color: #abacb5;
  padding-top: 12px;
  text-align: left !important;
}

.menu > .container > .App > .page-link:hover {
  color: #6bc909;
  cursor: pointer;
}

.grey-color {
  color: #80828f !important;
}

.grey-border-color {
  border: 1px solid #80828f !important;
}

.menu > .container > .social-icons-group > .social-icons {
  margin: 16px 8px 0px;
}

.tiktok-icon {
  border: 1.5px solid #6bc909;
  border-radius: 10px;
  width: 3rem;
  height: 3rem;
  justify-content: center;
  align-items: center;
  display: flex;
}

.tiktok-icon > img {
  height: 20px;
  /* height: 33%; */
}

.menu-language-background {
  background-color: #171923;
  border-bottom: 0px;
}

