/* The Close Button */
.close {
  color: #171923;
  /* float: right;
  margin-right: 3vw;
  font-size: 28px;
  font-weight: bold;
  margin-top: 24px; */
  position: absolute;
  /* right: 15px; */
  /* right: -15vw; */
  right: -40px;
  /* top: 76px; */
  /* top: -11px; */
  top: 0px;
  z-index: 100;
  font-size: 28px;
  font-weight: 500;
  background-color: #fff;
  border-radius: 50%;
  align-items: center;
  text-align: center;
  justify-content: center;
  display: flex;
  height: 25px;
  width: 25px;
  border: none;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

