.nft-gallery {
  padding: 10px 2px;
  margin: auto;
  text-align: center;
}

.nft-gallery-title {
  margin: 30px auto 20px;
  text-align: center !important;
}

.nft-gallery-leftline {
  display: flex;
  justify-content: right;
  padding: 5px !important;
  box-sizing: border-box;
}

.nft-gallery-rightline {
  display: flex;
  justify-content: left;
  padding: 5px !important;
  box-sizing: border-box;
}

.nft-gallery .plus-icon,
.nft-gallerydetail .plus-icon {
  margin-left: 240px;
  margin-top: -80px;
  cursor: pointer;
  background-image: radial-gradient(
    #222435 0%,
    transparent 60%,
    transparent 100%
  );
}

.nft-gallerydetail .plus-icon {
  margin-top: -69px;
}

@media only screen and (max-width: 420px) {
  .nft-gallery .plus-icon,
  .nft-gallerydetail .plus-icon {    
    position: fixed;
    bottom: 65px;
    right: 10px;
  }
  .nft-gallery-title {
    margin: 30px auto 30px;
  }
}

.nft-gallery .plus-icon svg,
.nft-gallerydetail .plus-icon svg {
  width: 90px;
  height: 90px;
  color: #6bc909;
  transform: rotate(-90deg);
  transition: 0.5s;
}

.nft-gallery .plus-icon svg:hover,
.nft-gallerydetail .plus-icon svg:hover {
  transform: rotate(90deg);
  transition: 0.5s;
}

.nft-gallerydetail {
  padding: 10px 0;
  margin: auto;
  text-align: center;
}

.nft-gallerydetail-title {
  display: flex;
  align-items: center;
  margin: 10px;
  cursor: pointer;
  color: #abacb5
}

.nft-gallerydetail-content {
  max-height: 680px;
  overflow: auto;
}

@media only screen and (max-width: 420px) {
  .nft-gallerydetail-content {
    max-height: calc(100vh - 140px);
    padding-top: 10px;
  }
}

.nft-gallerydetail-title:hover {
  color: #6bc909!important;
}

.nft-gallerydetail-title svg {
  color: #abacb5!important;
}

.nft-gallerydetail .nft-gallerydetail-img {
  width: 80%;
}

.nft-gallerydetail .nft-gallerdetail-itemDescription {
  color: #f76f3e;
  text-align: center;
  margin: 20px 0;
}

.nft-gallerydetail .nft-gallerydetail-btn {
  margin: 20px auto 50px auto;
  max-width: 280px;
}

.nft-gallerydetail .nft-gallerydetail-item {
  padding: 10px;
}

@media only screen and (max-width: 420px) {
  .nft-gallerydetail {
    width: 100%;
    padding: 0px 16px;
  }
}

.nft-gallerydetail .nft-detail-attr,
.nft-gallerydetail .nft-detail-claim {
  width: 100%;
  padding: 0 20px;
}

.nft-gallerydetail .bottom-popup-content {
  width: 375px;
  min-height: 670px;
}

@media only screen and (max-width: 420px) {
  .nft-gallerydetail .bottom-popup-content {
    width: 100%;
    margin-top: 180px !important;
  }
}

.nft-gallerydetail .bottom-popup-content label {
  margin-top: 20px;
}

.nft-gallerydetail .bottom-popup-content select {
  margin-top: 10px;
  width: 100px;
  height: 50px;
  border-radius: 5px;
  border: 0px;
  padding: 5px;
  font-family: 'Rajdhani-semibold';
  font-weight: 600;
  font-size: 16px;
  background: #2c2f45;
  color: #abacb5;
}

.nft-gallerydetail
  .bottom-popup-content
  .wallet-container
  .field-wrapper
  .textinput-wrapper {
  background: #2c2f45;
}

.nft-gallerydetail
  .bottom-popup-content
  .wallet-container
  .field-wrapper
  .textinput-wrapper.textarea-wrapper {
  height: 120px;
}

.nft-gallerydetail
  .bottom-popup-content
  .wallet-container
  .field-wrapper
  .textinput-wrapper
  input {
  background: #2c2f45;
  color: #abacb5;
}

.nft-gallerydetail
  .bottom-popup-content
  .wallet-container
  .field-wrapper
  .textinput-wrapper
  textarea {
  resize: none;
  font-family: 'Rajdhani-semibold';
  font-weight: 600;
  font-size: 16px;
  padding: 0px 11px;
}

.trait-alert-wrapper {
  margin-top: 80px;
  align-items: center;
}

.nft-gallery > .MuiGrid-root {
  max-height: 650px;
  overflow: auto;
}

@media only screen and (max-width: 420px) {
  .nft-gallery > .MuiGrid-root {
    max-height: calc(100vh - 140px);
  }
}

.nft-gallerydetail-content::-webkit-scrollbar,
.nft-gallerydetail
  .bottom-popup-content
  .wallet-container
  .field-wrapper
  .textinput-wrapper
  textarea::-webkit-scrollbar,
  .nft-gallery .MuiGrid-root::-webkit-scrollbar {
  /* background: #B8B6B5; */
  width: 0px;
}

.nft-gallery > .MuiGrid-root::-webkit-scrollbar {
  width: 3px;
}

.nft-gallery .MuiGrid-root::-webkit-scrollbar-thumb ,
.nft-gallerydetail-content::-webkit-scrollbar-thumb {
  background: #6bc909;
  border-radius: 10px;
}
